<template>
  <div class="monitor">
    <div class="maps" id="map_container">map</div>
    <div class="tabcont">
      <div class="content-r fr" v-show="showOrderList">
        <ul class="tab-title">
          <li :class="{'active': showAlarmState===0}" @click="showAlarmList(0)">
            待处理
          </li>
          <li :class="{'active': showAlarmState===1}" @click="showAlarmList(1)">
            处理中
          </li>
          <li :class="{'active': showAlarmState===2}" @click="showAlarmList(2)">
            处理完毕
          </li>
        </ul>
        <el-scrollbar style="height: 100%;" v-show="showAlarmState===0">
          <div class="tab-con _scroll">
            <ul class="_panel-box active">
              <li v-for="(alarm, index) in alarmsUntreat" :key="index" @click="showStep(0, alarm)">
                <p class="alarm-content">
                  <span :class="{'text-danger': !alarm.handleStatus}">{{alarm.data.time__}}</span> <span class="alarm-type">【{{alarm.data.type}}{{alarm.data.alarmReasonName}}】</span><span class="alarm-address">{{alarm.data.address}}</span>
                </p>
                <div class="more-info">
                  <p class="alarm-time">
                    报警时间：<span>{{alarm.data.createTime}}</span>
                  </p>
                  <p class="alarm-time">
                    联系人：<span>{{alarm.data.user_name}}/{{alarm.data.user_account}}</span>
                  </p>
                  <p class="alarm-time">
                    mac：<span>{{alarm.data.mac}}</span>
                  </p>
                  <!--<p class="alarm-time">-->
                    <!--详细地址：<span>{{alarm.data.address}}</span>-->
                  <!--</p>-->
                  <button class="btn btn-small-border allot" @click="sendOrder($event, alarm)" v-auth="'ALARM_S'" v-if="!alarm.handleStatus&&alarm.data.place_type !== 21">确认派单</button>
                  <p class="alarm-time text-danger" v-if="!alarm.handleStatus&&alarm.data.place_type == 21">
                    建议电话回访
                  </p>
                  <!--<button class="btn btn-border btn-small-border operation" @click="setMisdeclare(alarm)">设为误报</button>-->
                  <!--<button class="btn btn-border btn-small-border operation" @click="delay(alarm)">延时处理</button>-->
                </div>
              </li>
            </ul>
          </div>
        </el-scrollbar>
        <el-scrollbar style="height: 100%;" v-show="showAlarmState===1">
          <div class="tab-con _scroll" v-auth="'ALARM'">
            <ul class="_panel-box active">
              <li class="schedule" v-for="item in alarmsPending" :key="item.id"  @click="showStep(1, item)">
                <p class="alarm-content">
                  <span class="alarm-type">【{{item.alarmTypeName}}{{item.alarmType}}】</span><span class="alarm-address">{{item.address}}</span>
                </p>
                <div class="more-info">
                  <p class="alarm-time">
                    现场勘验：<span>{{item.handleUser}} / {{item.phone}}</span>
                  </p>
                  <p class="alarm-time">
                    派工管理员：<span>{{item.adminUser}}</span>
                  </p>
                  <p class="alarm-time">
                    派工时间：<span>{{item.createTime}}</span>
                  </p>
                  <p class="alarm-time">
                    mac：<span>{{item.mac}}</span>
                  </p>
                  <!-- <button class="btn btn-small-border allot" @click="handleOrder($event, item)" v-auth="'TASK_E'">处理工单</button> -->
                  <button class="btn btn-small-border allot" @click="handleOrder($event, item)" v-auth="'TASK_E'">处理工单</button>
                </div>
              </li>
            </ul>
          </div>
        </el-scrollbar>
        <el-scrollbar style="height: 100%;" v-show="showAlarmState===2">
          <div class="tab-con _scroll">
            <ul class="_panel-box active">
              <li class="schedule" v-for="item in alarmsComplete" :key="item.id"  @click="showStep(2, item)">
                <p class="alarm-content">
                  <span class="alarm-type">【{{item.alarmTypeName}}{{item.alarmType}}】</span><span class="alarm-address">{{item.address}}</span>
                </p>
                <div class="more-info">
                  <p class="alarm-time">
                    现场勘验：<span>{{item.handleUser}} / {{item.phone}}</span>
                  </p>
                  <p class="alarm-time">
                    派工管理员：<span>{{item.adminUser}}</span>
                  </p>
                  <p class="alarm-time">
                    mac：<span>{{item.mac}}</span>
                  </p>
                  <p class="alarm-time">
                    报警时间：<span>{{item.alarmTime}}</span>
                  </p>
                  <p class="alarm-time">
                    派工时间：<span>{{item.createTime}}</span>
                  </p>
                  <!--<p class="alarm-time">-->
                    <!--安装位置：<span>{{item.address}}</span>-->
                  <!--</p>-->
                  <p class="alarm-time">
                    处理结果：<span>{{item.memo}}</span>
                  </p>
                  <p class="alarm-time">
                    处理时间：<span>{{item.endTime}}</span>
                  </p>
                </div>
              </li>
            </ul>
          </div>
        </el-scrollbar>
      </div>
    </div>
    <handle-order ref="handleOrder_"></handle-order>
    <handle-step ref="handleStep"></handle-step>

  </div>
</template>

<script>
import { mapActions } from 'vuex'
import handleOrder from './components/dialogs/handle_order'
import handleStep from './components/dialogs/alarm_handle_steps'
import Load from './js/load_data'
import Subcrible from './js/subcrible_msg'

export default {
  name: 'Monitor',
  components: {
    handleOrder,
    handleStep
  },
  mounted() {
    // const container = document.getElementById('map_container')
    const map = Load.initMap('map_container')
    this.map = map
    Subcrible.init(map)
    this.initMonitorAlarms()

  },
  data() {
    return {
      showOrderList: true,
      showAlarmState: 0,
      showAll: false,
      map: undefined
    }
  },
  computed: {
    alarmsUntreat() {
      // console.log(this.$store.state.monitor.alarms.untreated)
      return this.$store.state.monitor.alarms.untreated
    },
    alarmsPending() {
      return this.$store.state.monitor.alarms.pending
    },
    alarmsComplete() {
      return this.$store.state.monitor.alarms.complete
    }
    // alarmsUntreatData() {
    //   return this.alarmsUntreat ? this.alarmsUntreat : []
    // }
  },
  methods: {
    ...mapActions([
      'initMonitorAlarms'
    ]),
    showAlarmList(state) {
      this.showAlarmState = state
    },
    handleOrder(e, order) {
      e.stopPropagation()
      this.$refs.handleOrder_.show(order)
    },
    showStep(type, item) {
      if (type === 0) {
        // const createTimes = item.data.createTime ? item.data.createTime.split('') : ['', '']
        const createTimes = ['', '']
        this.$refs.handleStep.show({
          alarmTime: createTimes[0],
          alarmDate: createTimes[1],
          alarmTypeName: item.data.type,
          address: item.data.address
        })
      } else {
        console.log(item)
        // const alarmTimes = item.alarmTime.split(' ')
        const alarmTimes = ['', '']
        // const createTimes = item.createTime ? item.createTime.split(' ') : ['', '']
        const createTimes = ['', '']
        // const createTimes = item.createTime ? item.createTime.split(' ') : ['', '']
        // const endTimes = item.endTime ? item.endTime.split(' ') : ['', '']
        const endTimes = ['', '']
        this.$refs.handleStep.show({
          alarmTime: alarmTimes[1],
          alarmDate: alarmTimes[0],
          alarmTypeName: item.alarmTypeName,
          address: item.address,
          createTime: createTimes[1],
          createDate: createTimes[0],
          handleUser: item.handleUser,
          handleUserPhone: item.phone,
          endTime: endTimes[1],
          endDate: endTimes[0],
          memo: item.memo
        })
      }
    }
  }
}
</script>

