import FlashDevices from './falsh_devices'
import pictures from './device_icons'
import store from '@/store'
import PointOverlay from './device_point_collection'

class CanvasOverlay extends BMap.Overlay {
  constructor(gridWidth) {
    super()
    this.gridWidth = gridWidth || 1000
  }
  initialize(map) {
    this._map = map
    // 闪光动画
    const canvas = this.canvas = document.createElement('canvas')
    canvas.style.cssText = 'position:absolute;left:0;top:0;'
    // 设备
    const canvas_ = this.deviceCanvas = document.createElement('canvas')
    canvas_.style.cssText = 'position:absolute;left:0;top:0;'
    this.initCanvasSize(map)
    const labelPane = map.getPanes().labelPane
    labelPane.style.zIndex = 100
    labelPane.appendChild(canvas)
    labelPane.appendChild(canvas_)
    this.showDevices = []
    this.setClickPoint()
    return this.deviceCanvas
  }
  initCanvasSize(map) {
    const size = map.getSize()
    if (this.width !== size.width) {
      this.width = this.canvas.width = this.deviceCanvas.width = size.width
    }
    if (this.height !== size.height) {
      this.height = this.canvas.height = this.deviceCanvas.height = size.height
    }
  }
  getDeviceShowStatus(code) {
    const showObj = store.state.monitor.showStatus
    if (code === 2) {
      return showObj.smoke
    } else if (code === 178) {
      return showObj.nbsmoke
    } else if (code === 2222) {
      return showObj.njsmoke
    } else if (code === 5) {
      return showObj.gas
    } else if (code === 181) {
      return showObj.nbgas
    } else if (code === 229) {
      return showObj.ingas
    } else if (code === 31) {
      return showObj.conmon
    } else if (code === 183) {
      return showObj.nboneBtnAlarm
    } else if (code === 7) {
      return showObj.oneBtnAlarm
    } else if (code === 26) {
      return showObj.patrolassistant
    } else if (code === 15) {
      return showObj.temperature
    } else if (code === 21) {
      return showObj.leakageTemperature
    } else if (code === 30) {
      return showObj.smetleakageTemperature
    } else if (code === 22) {
      return showObj.fireHydrantWaterPress
    } else if (code === 23) {
      return showObj.waterLevel
    } else if (code === 24) {
      return showObj.fireEngine
    } else if (code === 11) {
      return showObj.elestric
    } else if (code === 91) {
      return showObj.obstacle
    } else {
      return false
    }
  }
  filterDeviceByType(pixel, boundWidth, boundHeight) {
    const devices = store.state.monitor.AllDevices
    // debugger
    const showDevices = []
    const alarmDevices = []
    const points = []
    const map = this._map
    devices.forEach(d => {
      if (!d.mapPoint) {
        d.mapPoint = new BMap.Point(d.lng, d.lat)
      }
      const px = map.pointToOverlayPixel(d.mapPoint)
      const x = px.x - pixel.x
      const y = px.y - pixel.y
      if (this.getDeviceShowStatus(d.olType || d.type) && x >= 0 && y >= 0 && x <= boundWidth && y <= boundHeight) {
        const flashDevice = new FlashDevices({ x, y, showType: d.showType, img: pictures.getPicture((d.olType || d.type), 1) })
        if (flashDevice.showType === 3) {
          alarmDevices.push(flashDevice)
        } else {
          showDevices.push(flashDevice)
        }
        points.push(d)
      }
    })
    showDevices.push(...alarmDevices)
    this.showDevices = showDevices
    return points
  }
  drawAction(showDevices, ctx, boundWidth, boundHeight, drawDeviceFlg) {
    clearTimeout(this.drawTimeout)
    let deviceCtx
    if (drawDeviceFlg) {
      deviceCtx = this.deviceCanvas.getContext('2d')
      deviceCtx.clearRect(0, 0, boundWidth, boundHeight)
    }
    ctx.clearRect(0, 0, boundWidth, boundHeight)
    showDevices.forEach(d => {
      d.render(ctx, deviceCtx)
    })
    this.drawTimeout = setTimeout(() => {
      this.drawAction(showDevices, ctx, boundWidth, boundHeight, false)
    }, 250)
  }
  setClickPoint() {
    const pointOverlay = new PointOverlay(this._map, this.showDevices)
    this._map.addOverlay(pointOverlay)
    this.clickPoints = pointOverlay
  }
  draw() {
    clearTimeout(this.drawTimeout)
    const map = this._map
    this.initCanvasSize(map)
    const bounds = map.getBounds()
    const sw = bounds.getSouthWest()
    const ne = bounds.getNorthEast()
    const pixel = map.pointToOverlayPixel(new BMap.Point(sw.lng, ne.lat))
    this.deviceCanvas.style.left = this.canvas.style.left = pixel.x + 'px'
    this.deviceCanvas.style.top = this.canvas.style.top = pixel.y + 'px'
    const boundWidth = this.canvas.width
    const boundHeight = this.canvas.height
    const ctx = this.canvas.getContext('2d')
    ctx.globalCompositeOperation = 'lighter'
    this.clickPoints.setPoints(this.filterDeviceByType(pixel, boundWidth, boundHeight))
    this.drawAction(this.showDevices, ctx, boundWidth, boundHeight, true)
  }
}

export default CanvasOverlay
