import wsUtil from './ws_util'
import { getItem } from '@/utils/funcs'
import constant from '@/common/constant'
import Store from '@/store'
import overlayDom from './overlay_dom'
import loadMap from './load_data'
import roleUtil from '@/utils/role_util'
import msgUtil from '@/utils/msgUtil'
import router from '@/router'

const deviceTypes = constant.deviceTypes
let timeTaskRA

function addMapDeviceMarker(json, map, olType, url) {
  Store.commit('closeAllAlarm')
  Store.commit('showTypeDevices', { typeName: olType.alias, value: true })
  const id = `mdm_${String(Math.random()).slice(2, 12)}_${Date.now()}`
  const infoBox = overlayDom.popInfoBox(map, json, id, olType.code)
  // console.log(router.currentRoute.path)
  var dqpath = router.currentRoute.path
  if (dqpath != '/index/monitor/monitor-center') {
    // console.log(json)
    const detail = `<a class='btn' style='padding:0 10px' href="javascript:void(0)" onclick="appGlobal.openDeviceDetail('${json.mac}', ${olType.code})">详情</a>`
    if (detail) {
      // console.log(json.device_name)
      if (json.device_name) {
        msgUtil.errormsg(json.device_name + '告警', '地址:' + json.address, detail)
      } else {
        const device = findDeviceByCode(json.types)
        // console.log(device)
        msgUtil.errormsg(device.name + '告警', '地址:' + json.address, detail)
      }
    }
  }
  infoBox.open(new BMap.Point(json.lng, json.lat))
  Store.commit('addUntreated', {
    $$id: id,
    infoBox,
    data: json,
    alarmStatus: 1
  })
  // handle alarm
  const strs = json.createTime.split(/[\s-:]/g)
  const cTime = new Date(strs[0], strs[1] - 1, strs[2], strs[3], strs[4], strs[5]).getTime()
  if (Date.now() - cTime >= 5000) {
    if (timeTaskRA) {
      clearTimeout(timeTaskRA)
    }
    timeTaskRA = setTimeout(() => {
      const ids = Store.state.monitor.alarms.untreated.map(a => a.data.id)
      ids.length > 0 && Store.dispatch('filterHandledAlarm', ids)
    }, 1000)
  }
  loadMap.reDraw()
  window.appGlobal.playVideo(olType.code)
  setTimeout(() => {
    window.appGlobal.closeInfoBox(3, id)
  }, 20000)
}

function init(map, url) {
  wsUtil.init()
  const topic = `iot/warning/user/${getItem(constant.sysuser)}`
  console.log('topic', topic)
  wsUtil.register(topic, (payload_) => {
    // console.log(payload_)
    let payload = payload_
    if (typeof payload_ === 'string') {
      payload = JSON.parse(payload_)
      // console.log(payload)
    }
    // console.log(roleUtil.hasAuth(`D${payload.types}V`))
    if (roleUtil.hasAuth(`D${payload.types}V`)) {
      // debugger
      // console.log(payload)
      payload.showType = 3
      payload.olType = payload.types
      if (payload.alarmReasonName) {

      } else {
        payload.alarmReasonName = constant.alarmReasons[payload.alarmReason] || '设备报警'
      }
      // const types = payload.types
      const device = findDeviceByCode(payload.types)
      if (device) {
        payload.type = device.name
        addMapDeviceMarker(payload, map, device, url)
      }
    }
  })
}

function findDeviceByCode(code) {
  let device_
  Object.keys(deviceTypes).some(key => {
    const device = deviceTypes[key]
    if (device.code === code) {
      device_ = device
      return true
    }
    return false
  })
  return device_
}

function close() {
  wsUtil.close()
}

export default {
  init,
  close
}
