class FlashDevices {
  constructor(options) {
    this.x = options.x
    this.y = options.y
    this.showType = options.showType || 1
    this.img = options.img
    const defaultConfig = this.defaultConfig(this.showType)
    this.imgWidth = options.imgWidth || defaultConfig.imgWidth
    this.imgHeight = options.imgHeight || defaultConfig.imgHeight
    this.maxSize = options.maxSize || defaultConfig.maxSize
    this.minSize = options.minSize || defaultConfig.minSize
    this.initSize()
  }
  defaultConfig(showType) {
    if (showType === 3) {
      return {
        imgWidth: 16,
        imgHeight: 14,
        maxSize: 25,
        minSize: 10
      }
    } else {
      return {
        imgWidth: 16,
        imgHeight: 14,
        maxSize: 12,
        minSize: 8
      }
    }
  }
  initSize() {
    this.size = ~~(this.minSize + Math.random() * (this.maxSize - this.minSize))
  }
  nextSize() {
    if (this.size === this.maxSize) {
      this.size = this.minSize
    } else {
      this.size++
    }
  }
  getGradient(ctx) {
    const gradient = ctx.createRadialGradient(this.x, this.y, 0, this.x, this.y, this.size)
    if (this.showType === 3) {
      gradient.addColorStop(0, 'rgba(255,52,91,0.85)')
      gradient.addColorStop(1, 'rgba(255,52,91,0.35)')
    } else {
      gradient.addColorStop(0, 'rgba(0,255,195,0.65)')
      gradient.addColorStop(1, 'rgba(0,255,195,0)')
    }
    return gradient
  }
  render(flashCtx, deviceCtx) {
    if (this.img) {
      deviceCtx && deviceCtx.drawImage(this.img, this.x - this.imgWidth / 2, this.y - this.imgHeight / 2)
      if (this.showType !== 2) {
        flashCtx.beginPath()
        flashCtx.fillStyle = this.getGradient(flashCtx)
        flashCtx.arc(this.x, this.y, this.size, Math.PI * 2, false)
        flashCtx.fill()
      }
      this.nextSize()
    }
  }
}

export default FlashDevices
