import { getItem } from '@/utils/funcs'
import constant from '@/common/constant'
import { validateToken } from '@/api/user'
import store from '@/store'

const reconnectTimeout = 2000
// const host = 'mqtt.onecitycare.com'
// const host = 'test.91mogo.com'
// const port = 9001
const defaultOption = {
  userName: constant.mqttConfig.username,
  password: constant.mqttConfig.username,
  timeout: 3,
  onSuccess: onConnect,
  onFailure: onFailure,
  cleanSession: false
}
// const demoData = {"province": "\u5c71\u897f\u7701", "alarmReason": 1, "user_account": "17138972336", "county": "\u674f\u82b1\u5cad\u533a", "mac": "d50f110000f3", "street": "\u65b0\u5efa\u8def81\u53f7", "address": "\u592a\u539f\u5e02\u59d4\u5927\u96629-1-105", "lat": "37.73855", "lng": "112.601593", "createTime": "2018-05-11 18:12:30", "types": 2, "building": "9", "city": "\u592a\u539f\u5e02", "room": "105", "floor": "1", "id": 762700, "alias": "\u592a\u539f\u5e02\u59d4\u5927\u9662", "user_name": "\u8d75\u5b87"}
let connecting = false
let mqtt__
let subscribeMap = {}
let reconnectTask
let needConnect = false
function getPath() {
  return `ws_client_${getItem(constant.sysuser)}`
}

function mqttSubscribe(topic) {
  mqtt__.subscribe(topic, { qos: 1 })
}

function onConnect() {
  store.commit('changeMqttStatus', 1)
  connecting = true
  const keys = Object.keys(subscribeMap)
  // msgUtil.message('连接mqtt成功')
  console.log('连接mqtt成功', keys)
  keys.forEach((key) => {
    mqttSubscribe(key)
    console.log(`mqttSubscribe: ${getPath()}, ${key}`)
  })
  send()
}

function onMessageArrived(message) {
  // console.log(message)
  try {
    const topic = message.destinationName
    const payload = message.payloadString
    const keys = Object.keys(subscribeMap)
    keys.forEach((key) => {
      if (topic.indexOf(key.replace('#', '')) === 0) {
        (subscribeMap[key] || []).forEach(cb => cb(payload))
      }
    })
  } catch (e) {
    console.log('mqtt onMessageArrived error', e)
  }
}

function register(url, cb) {
  if (!subscribeMap[url]) {
    subscribeMap[url] = [cb]
    if (connecting) {
      mqttSubscribe(url)
    }
  } else {
    subscribeMap[url] = [cb]
  }
}

function resetMqtt() {
  clearTimeout(reconnectTask)
  if (mqtt__) {
    mqtt__.onConnectionLost = function() {
      connecting = false
    }
    if (mqtt__.isConnected()) {
      mqtt__.disconnect()
    }
  }
  mqtt__ = undefined
  console.log('mqtt已重置')
}

function send() {
  function getNowFormatDate() {
    var date = new Date()
    var seperator1 = '-'
    var seperator2 = ':'
    var month = date.getMonth() + 1
    var strDate = date.getDate()
    if (month >= 1 && month <= 9) {
      month = '0' + month
    }
    if (strDate >= 0 && strDate <= 9) {
      strDate = '0' + strDate
    }
    var min = date.getMinutes()
    if (min >= 0 && min <= 9) {
      min = '0' + min
    }
    var ho = date.getHours()
    if (ho >= 0 && ho <= 9) {
      ho = '0' + ho
    }
    var sec = date.getSeconds()
    if (sec >= 0 && sec <= 9) {
      sec = '0' + sec
    }
    var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + ho + seperator2 + min + seperator2 + sec
    return currentdate
  }
  var s = '{"time":"' + getNowFormatDate() + '", "username":"' + (getItem(constant.sysuser)) + '"}'
  var message = new Paho.MQTT.Message(s)
  message.destinationName = `iot/login/status/${getItem(constant.sysuser)}/2`
  mqtt__.send(message)
  setInterval(function() {
    var s = '{"time":"' + getNowFormatDate() + '", "username":"' + (getItem(constant.sysuser)) + '"}'
    var message = new Paho.MQTT.Message(s)
    message.destinationName = `iot/login/status/${getItem(constant.sysuser)}/2`
    mqtt__.send(message)
  }, 290000)
}

function close() {
  needConnect = false
  resetMqtt()
  subscribeMap = {}
  console.log('mqtt已关闭')
}

function init() {
  if (!mqtt__ && !needConnect) {
    needConnect = true
    MQTTconnect()
  }
}

function MQTTconnect() {
  validateToken().then(success => {
    if (!mqtt__ || !mqtt__.isConnected()) {
      mqtt__ = new Paho.MQTT.Client(constant.mqttConfig.host, constant.mqttConfig.port, getPath())
      mqtt__.onConnectionLost = onConnectionLost
      mqtt__.onFailure = onFailure
      mqtt__.onMessageArrived = onMessageArrived
      mqtt__.connect(defaultOption)
    }
  }, error => {
    close()
    console.log('异地登录', error)
  })
}

function onConnectionLost(response) {
  store.commit('changeMqttStatus', 2)
  connecting = false
  // msgUtil.message('mqtt链接丢失', '正在重新连接')
  console.log('mqtt链接丢失', response)
  clearTimeout(reconnectTask)
  if (needConnect) {
    reconnectTask = setTimeout(() => {
      if (needConnect) {
        MQTTconnect()
      }
    }, reconnectTimeout)
  }
}

function onFailure(message) {
  store.commit('changeMqttStatus', 0)
  clearTimeout(reconnectTask)
  if (needConnect) {
    reconnectTask = setTimeout(() => {
      if (needConnect) {
        MQTTconnect()
      }
    }, reconnectTimeout)
  }
}

export default {
  init,
  register,
  close,
  send
}
