import overlayDom from './overlay_dom'
import store from '@/store'
import { findDevice } from '@/api/device'
import msgUtil from '@/utils/msgUtil'

class DevicePointCollection extends BMap.PointCollection {
  constructor(map, devices) {
    const points = []
    const dataMap = {}
    devices.forEach(d => {
      points.push(new BMap.Point(d.lng, d.lat))
      dataMap[`${+d.lng},${+d.lat}`] = d
    })
    super(points, {
      size: BMAP_POINT_SIZE_BIG,
      shape: BMAP_POINT_SHAPE_CIRCLE,
      color: 'rgba(0, 0, 0, 0)'
    })
    this.dataMap = dataMap
    // this.showObj = {}
    this._map = map
    this.setOnclick()
  }
  getDataById(id) {
    return this.dataMap[id]
  }
  generateId() {
    const id = `mdm_${String(Math.random()).slice(2, 12)}_${Date.now()}`
    return id
  }
  showInfowindow(device) {
    findDevice(device.mac).then(resp => {
      const infoId = this.generateId()
      device.showType = resp.data.showType = 1
      const infoBox = overlayDom.popInfoBox(this._map, resp.data, infoId, device.olType || device.type)
      infoBox.$$id = infoId
      infoBox.open(new BMap.Point(device.lng, device.lat))
      store.commit('addInfos', infoBox)
    }, error => {
      msgUtil.error('查询设备数据失败', msgUtil.findErrorMsg(error))
    })
  }
  setOnclick() {
    this.addEventListener('click', (e) => {
      const device = this.getDataById(`${e.point.lng},${e.point.lat}`)
      if (device) {
        this.showInfowindow(device)
      }
    })
  }
  setPoints(devices) {
    const points = []
    devices.forEach(d => {
      points.push(d.mapPoint)
      this.dataMap[`${+d.lng},${+d.lat}`] = d
    })
    super.setPoints(points)
  }
}
export default DevicePointCollection
