import constant from '@/common/constant'
import { getItem, setItem } from '@/utils/funcs'
import CanvasOverlay from './canvas_overlay'

export default (function() {
  let deviceFlashOverlay
  function initMap(container) {
    const map = new BMap.Map(container, { enableMapClick: false })
    const province = getItem(constant.province)
    const city = getItem(constant.city)
    const county = getItem(constant.county)
    if (getItem(constant.mapdragendlng) && getItem(constant.mapdragendlat) && getItem(constant.mapzoomend)) {
      map.centerAndZoom(new BMap.Point(getItem(constant.mapdragendlng), getItem(constant.mapdragendlat)), getItem(constant.mapzoomend))
    } else {
      if (county) {
        map.centerAndZoom(county, 15)
      } else if (city) {
        map.centerAndZoom(city, 13)
      } else if (province) {
        map.centerAndZoom(province, 9)
      } else {
        map.centerAndZoom('北京市', 5)
      }
    }
    map.enableScrollWheelZoom(true) // 启用滚轮放大缩小
    map.addEventListener('dragend', function() {
      var center = map.getCenter()
      // console.log("地图中心点变更为：" + center.lng + ", " + center.lat)
      setItem(constant.mapdragendlng, center.lng)
      setItem(constant.mapdragendlat, center.lat)
    })
    map.addEventListener('zoomend', function() {
      // console.log("地图缩放至：" + this.getZoom() + "级")
      setItem(constant.mapzoomend, this.getZoom())
    })
    setTimeout(() => {
      if (map) {
        const top_left_control = new BMap.ScaleControl({ anchor: BMAP_ANCHOR_TOP_LEFT }) // 左上角，添加比例尺
        map.addControl(top_left_control)
        const top_left_navigation = new BMap.NavigationControl() // 左上角，添加默认缩放平移控件
        map.addControl(top_left_navigation)
        map.addControl(new BMap.MapTypeControl(
          {
            mapTypes: [
              BMAP_NORMAL_MAP,
              BMAP_HYBRID_MAP
            ] }
        ))
        const ctrlOve = new window.BMap.OverviewMapControl({
          anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
          isOpen: 1
        })
        map.addControl(ctrlOve)
        deviceFlashOverlay = loadFlashDevice(map)
      }
    }, 1000)
    // const tt = map.getPanes().labelPane
    // Object.keys(constant.deviceTypes).forEach((key) => {
    //   const div = document.createElement('div')
    //   div.setAttribute('id', `table_${constant.deviceTypes[key].code}`)
    //   div.setAttribute('style', 'display: none;')
    //   tt.appendChild(div)
    // })
    // 报警历史
    // const ul = document.createElement('ul')
    // ul.setAttribute('class', 'warning-list')
    // ul.setAttribute('id', 'map_warning_list')
    // tt.appendChild(ul)
    map.clearOverlays()
    map.setMapStyle({ style: 'midnight' })
    return map
  }

  function loadFlashDevice(map) {
    const deviceFlashOverlay = new CanvasOverlay()
    map.addOverlay(deviceFlashOverlay)
    return deviceFlashOverlay
  }

  function reDraw() {
    deviceFlashOverlay && deviceFlashOverlay.draw()
  }

  return {
    initMap,
    reDraw
  }
})()
