function loadIcon(deviceType, type, id, point) {
  const className = type === 1 ? 'animation-online' : 'animation-warning'
  const popup = new Popup(`device_ico_${id}`, BMap.Overlay.getZIndex(point.lat), 'device-icon', `static/img/device_icon/d_${deviceType}_${type}.png`, className)
  // const div = popup.div
  // div.style.background = `url(static/img/device_icon/device_${deviceType}_${type}.png) no-repeat`
  return popup
}

function fireEngine(type, id, point) {
  const popup = new Popup(`fe_${id}`, BMap.Overlay.getZIndex(point.lat), 'MapList')
  popup.div.style.background = 'url(static/assets/occ_img/fire_engine.png) no-repeat'
  return popup
}

/**
 * 获取overlay 的dom
 * @param olType 类型(smoke...)
 * @param args {type: 1.正常, 3.告警, id: deviceId, point: 定位点}
 */
function getOverlayDom(deviceType, ...args) {
  return loadIcon(deviceType, ...args)
}



export default {
  popInfoBox,
  getOverlayDom
}
